const userStyle = {
    margin:10,   
    width:100,
    display: 'flex',
}

const userInfoStyle = {
    margin:10
}

const avatarStyle = {
    borderRadius: '50%',
}

const spacing = {
    marginTop: 8,
}

function getCurrentUser() {
    return {
        avatarUrl : 'https://placekitten.com/g/64/64',
        name: "TommyCakes",
        status: "Preminum"
    }
}

function Avatar(props) {
    return (
        <img className="Avatar" style={avatarStyle}
            src={props.user.avatarUrl}
            alt={props.user.name}
          />
    )
}

function UserInfo(props) {
    return (
        <div className="UserInfo" style={userInfoStyle}>
            <Avatar user={props.user}/>

            <div className="UserInfo" style={spacing}>
            {props.user.name}
            {props.user.status}
          </div>        
            <div className="Membership-status" style={spacing}>
            
            </div> 

        </div>
    );
}

function User(props) {
    return (
      <div className="User" style={userStyle}>               
        <UserInfo user={props.user}/>      
      </div>
    );
  }

  const Profile = () => (
      <div>
        <User
            user={getCurrentUser()}
        />
      </div>
  )

  export default Profile